<template>
  <div>
    <van-notice-bar
        wrapable
        :scrollable="false"
        text="本页面演示商户流程，请自行注册商户，获取API Key后结合自身业务进行开发！"
    />
    <van-divider :style="{ color: '#1989fa', borderColor: '#1989fa', padding: '0 16px',margin:'50px 0 50px 0' }">
            <span style="font-size:18px ">
                商户Demo登录
            </span>
    </van-divider>
    <van-cell-group style="margin-top: 30px;margin-bottom: 12px;">
      <van-field
          v-model="username"
          type="text"
          size="large"
          label="用户名："
          placeholder="请输入用户名jack/sam/john/Bill"
      />
      <van-field
          v-model="password"
          size="large"
          type="password"
          label="密码："
          placeholder="请输入密码123456"
      />
    </van-cell-group>

    <div class="button-warp">
      <van-button
          type="info"
          block
          @click="toLogin" >
        登 录
      </van-button>
    </div>
  </div>
</template>

<script>
import {postAction} from '@/api'
import {
  ActionSheet, Area,
  Button, Calendar,
  Cell,
  CellGroup, Circle,
  Col,
  DatetimePicker,
  Divider,
  Field,
  Icon, Notify,
  Picker, Popup, Radio,
  RadioGroup,
  Row, SwipeCell, Switch, Toast,Checkbox,NoticeBar
} from "vant";


export default {
  name: "login",
  components: {
    [Col.name]: Col,
    [Row.name]: Row,
    [Icon.name]: Icon,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Button.name]: Button,
    [Field.name]: Field,
    [Picker.name]: Picker,
    [ActionSheet.name]: ActionSheet,
    [DatetimePicker.name]: DatetimePicker,
    [Divider .name]: Divider,
    [RadioGroup .name]: RadioGroup,
    [Radio.name]: Radio,
    [Switch.name]: Switch,
    [Popup.name]: Popup,
    [Area.name]: Area,
    [Calendar.name]: Calendar,
    [SwipeCell.name]: SwipeCell,
    [Notify.name]: Notify,
    [Toast.name]: Toast,
    [Circle.name]: Circle,
    [Checkbox.name]: Checkbox,
    [NoticeBar.name]: NoticeBar ,
  },
  data(){
    return{
      username:'',
      password:'',
      testInt:1055830080527936
    }
  },
  methods:{
    toLogin(){
      Toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true,
        message: '登录中...'
      })
      const params={
        userName: this.username,
        pswd:this.password
      }
      postAction('/v1/useramount/AppUserLogin',params)
          .then(res=>{
            if (res.success){
              // debugger
              this.$store.commit('SET_USERNAME',this.username)
              this.$store.commit('SET_PASSWORD',this.password)
              this.$router.push('/gpay/pay')
            }else{
              this.$notify({type:'danger',message:res.message})
            }
          })
          .finally(()=>{
            Toast.clear()
          })
    }
  }
}
</script>


<style lang="less" scoped>
.title-warp{
  background-color: #1989FA;
  padding:45px 0;
  .index-title{
    color:#ffffff;
    text-align: center;
    font-size: 20px;
  }
  .jyj-title{
    color:#ffffff;
    text-align: center;
    font-size: 28px;
    margin-bottom: 15px;
  }
}
.button-warp{
  padding:8px;
  background-color: #ffffff;
  margin-top: 30px
}
.width150{
  padding-right: 80px;
}
</style>